

import Vue from 'vue'
import Vuex from 'vuex';
import App from './App'
import router from './router'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


Vue.config.productionTip = false

/* eslint-disable no-new */
Vue.use(Vuex);
Vue.use(LoadingPlugin);
Vue.use(ToastPlugin);
Vue.use(AlertPlugin);
Vue.use(ConfirmPlugin);

var store = new Vuex.Store({// store对象
state: {
states: 'turn-on',
userAccount: {



},
loginStatus: 0 
},
getters: {
addUserInfo: (state) => {
return state.states + 'change';
}
},
mutations: {
setTransition (state, states) {
state.states = states;
},
setUserInfo (state, userAccount) {
state.userAccount = userAccount;
},
setLoginStatus (state, loginStatus) {
state.loginStatus = loginStatus;
}
}
});

new Vue({
el: '#app',
router,
components: { App },
template: '<App/>'
})



