
function render(rawData) {
 
  var data = splitData(rawData);
   var downColor = '#00da3c';
   var upColor= '#ec0000';
 
   function splitData(rawData) {
     var categoryData = [];
     var values = [];
     var volumes = [];
     for (var i = 0; i < rawData.length; i++) {
         categoryData.push(rawData[i].splice(0, 1)[0]);
         values.push(rawData[i]);
         volumes.push([i, rawData[i][4], rawData[i][0] > rawData[i][1] ? 1 : -1]);
     }
   
     return {
         categoryData: categoryData,
         values: values,
         volumes: volumes
     };
   }
 
   function calculateMA(dayCount, data) {
     var result = [];
     for (var i = 0, len = data.values.length; i < len; i++) {
         if (i < dayCount) {
             result.push('-');
             continue;
         }
         var sum = 0;
         for (var j = 0; j < dayCount; j++) {
             sum += data.values[i - j][1];
         }
         result.push(+(sum / dayCount).toFixed(3));
     }
     return result;
 }
 
   var option = {
     animation: false,
     tooltip: {
         trigger: 'axis',
         axisPointer: {
             type: 'cross'
         },
         formatter: function (param) {
            param = param[0]
            // console.log(param)
            return [
              '时间: ' + param.name + '<hr size=1 style="margin: 3px 0">',
              '开盘价: ' + param.data[1] + '<br/>',
              '收盘价: ' + param.data[2] + '<br/>',
              '最低价: ' + param.data[3] + '<br/>',
              '最高价: ' + param.data[4] + '<br/>',
              '交易量: ' + param.data[5] + '<br/>'
            ].join('')
          },
         backgroundColor: 'rgba(245, 245, 245, 0.8)',
         borderWidth: 1,
         borderColor: '#ccc',
         padding: 10,
         textStyle: {
             color: '#000'
         },
         position: function (pos, params, el, elRect, size) {
             var obj = {top: 10};
             obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 30;
             return obj;
         }
     },
     
     axisPointer: {
         link: {xAxisIndex: 'all'},
         label: {
             backgroundColor: '#777'
         }
     },
     toolbox: {
         feature: {
             dataZoom: {
                 yAxisIndex: false
             },
             brush: {
                 type: ['lineX', 'clear']
             }
         }
     },
     grid: [{
       left: '12%',
       top: '5%',
       right: '8%',
       height: '75%'
     },
     {
       left: '12%',
       right: '8%',
       top: '95%',
       height: '15%'
     }],
    
     xAxis: [
         {
             type: 'category',
             data: data.categoryData,
             scale: true,
             boundaryGap : false,
             axisLine: {onZero: false},
             axisLine: { lineStyle: { color: '#cccccc' } },
             splitLine: {show: false},
             splitNumber: 20,
             min: 'dataMin',
             max: 'dataMax',
             axisPointer: {
                 z: 100
             },
             axisLabel: {
              show: true,
              textStyle: {
                  color: '#fff'
              }
          }
         },
         {
             type: 'category',
             gridIndex: 1,
             data: data.categoryData,
             scale: true,
             boundaryGap : false,
             axisLine: {onZero: false},
             axisTick: {show: false},
             splitLine: {show: false},
             axisLabel: {show: true,
              textStyle: {
                color: '#ffffff'
              } 
            },
             splitNumber: 20,
             min: 'dataMin',
             max: 'dataMax'
         }
     ],
     yAxis: [
         {
             scale: true,
             axisLine: { lineStyle: { color: 'rgba(0,0,0,0)' } },
             splitArea: {
                 show: false
             },
             axisLabel: {show: true,
              textStyle: {
                color: '#ffffff'
              } 
            },
         },
         {
             scale: true,
             gridIndex: 1,
             splitNumber: 2,
             axisLabel: {show: false},
             axisLine: {show: false},
             axisTick: {show: false},
             splitLine: {show: false},
         }
     ],
     dataZoom: [
         {
             type: 'inside',
             xAxisIndex: [0, 1],
             start: 0,
             end: 100
         },
         {
             show: false,
             xAxisIndex: [0, 1],
             type: 'slider',
             top: '85%',
             start: 98,
             end: 100
         }
     ],
     series: [
         {
             type: 'candlestick',
             data: data.values,
             itemStyle: {
                 normal: {
                     color: upColor,
                     color0: downColor,
                     borderColor: null,
                     borderColor0: null
                 }
             },
             tooltip: {
                 formatter: function (param) {
                     param = param[0];
                     return [
                         'Date: ' + param.name + '<hr size=1 style="margin: 3px 0">',
                         '开盘价:' + param.data[0] + '<br/>',
                         '收盘价:' + param.data[1] + '<br/>',
                         '最高价:' + param.data[3] + '<br/>',
                         '最低价:' + param.data[2] + '<br/>'
                     ].join('');
                 }
             }
         },
        //  {
        //      name: 'Volume',
        //      type: 'bar',
        //      xAxisIndex: 1,
        //      yAxisIndex: 1,
        //      data: data.volumes,
        //      barWidth: '50%',
        //  }
     ]
 }
   
 
   return option;
 }
 
 export default render


